<template>
    <div class="menu-setting">
        <div v-if="$route.name == '配置定时菜单'">
            <Button class="create" type="primary" to="/manage/gonghao/timingMenu/create">配置新菜单栏</Button>
            <Table class="menu-table" stripe :columns="menuHeader" :data="menuData"></Table>
        </div>
        <router-view />
    </div>
</template>

<script>
export default {
  data() {
    return {
      menuData: [],
      menuHeader: [
        {
          key: "title",
          title: "标题",
          align: "center"
        },
        {
          key: "time",
          title: "时间",
          align: "center"
        },
        {
          title: "Action",
          align: "center",
          render: (h, params) => {
            let that = this;
            return h("div", [
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.updateOne(params.row);
                    }
                  }
                },
                "编辑"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.deleteOne(params.row);
                    }
                  }
                },
                "删除"
              )
            ]);
          }
        }
      ]
    };
  },
  mounted() {
    this.showMenuList();
  },
  methods: {
    showMenuList() {
      this.$axios.get("/menuTime").then(res => {
        for (let i = 0; i < res.data.data.length; i++) {
          let time = res.data.data[i].time;
          time = this.formatDate(time);
          res.data.data[i].time = time;
        }
        this.menuData = res.data.data;
      });
    },
    updateOne(row) {
      this.$router.push({
        name: "修改定时菜单",
        params: row
      });
    },
    deleteOne(row) {
      this.$Modal.confirm({
        title: "提示:",
        content: "确认删除这条内容吗？",
        onOk: () => {
          this.$axios.get("/menuTime/del", { params: { id: row._id } })
          .then(res => {
            this.showMenuList();
            this.$Message.info(res.data.success);
          });
        }
      });
      
    },
    formatDate(time) {
      var date = new Date(time);
      var year = date.getFullYear(),
        month = date.getMonth() + 1,
        day = date.getDate(),
        hour = date.getHours(),
        min = date.getMinutes(),
        sec = date.getSeconds();
      var newTime =
        year +
        "-" +
        (month < 10 ? "0" + month : month) +
        "-" +
        (day < 10 ? "0" + day : day) +
        " " +
        (hour < 10 ? "0" + hour : hour) +
        ":" +
        (min < 10 ? "0" + min : min) +
        ":" +
        (sec < 10 ? "0" + sec : sec);
      return newTime;
    }
  },
  watch: {
    $route(to, from) {
      if (from.name == "新建定时菜单" || from.name == "修改定时菜单") {
        this.showMenuList();
      }
    }
  }
};
</script>

<style scoped>
.create {
  margin-bottom: 30px;
  float: left;
}

.menu-table {
  clear: both;
}
</style>
